html,
body {
  margin: 0;
  height: 100%;
}

.app-wrapper {
  height: 100%;
  display: grid;
  grid-template-rows: 100px 1fr 100px;
  grid-template-columns: 200px 1fr;
  grid-template-areas:
    "header header"
    "nav main"
    "nav footer";
  grid-gap: 20px;
}

.app-wrapper-content {
  grid-area: main;
  background-color: #f5e9d7;
}

.footer {
  grid-area: footer;
  background-color: #f2bda7;
}
